.telegram-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    min-height: 100vh;
    color: white;
}

.header {
    text-align: center;
    margin-bottom: 30px;
    flex-direction: column;
}

.header h1 {
    font-size: 24px;
    margin-bottom: 10px;
    font-weight: 600;
}

.header p {
    opacity: 0.8;
    margin-bottom: 20px;
}

.buttons-container {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-bottom: 30px;
}

.tg-button {
    position: relative;
    padding: 15px 20px;
    border: none;
    border-radius: 12px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.tg-button.primary {
    background: linear-gradient(45deg, #0088cc, #00a2e8);
    color: white;
}

.tg-button.secondary {
    background: linear-gradient(45deg, #2ecc71, #27ae60);
    color: white;
}

.tg-button.danger {
    background: linear-gradient(45deg, #e74c3c, #c0392b);
    color: white;
}

.tg-button.warning {
    background: linear-gradient(45deg, #efde74, #c0a72b);
    color: white;
}

.tg-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.tg-button:active {
    transform: translateY(0);
}

.counter {
    background: rgba(255, 255, 255, 0.2);
    padding: 4px 12px;
    border-radius: 20px;
    font-size: 14px;
    font-weight: 600;
}

.stats {
    background: rgba(255, 255, 255, 0.1);
    padding: 20px;
    border-radius: 12px;
    margin-bottom: 20px;
    backdrop-filter: blur(10px);
}

.stats h3 {
    margin-bottom: 15px;
    text-align: center;
    font-weight: 600;
}

.stats-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
}

.stats-grid-two {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
}

.stat-item {
    text-align: center;
    padding: 10px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
}

.stat-item span {
    display: block;
    font-size: 12px;
    opacity: 0.8;
    margin-bottom: 5px;
}

.stat-item strong {
    font-size: 18px;
    color: #fff;
}

.main-action-button {
    width: 100%;
    padding: 15px;
    background: linear-gradient(45deg, #9b59b6, #8e44ad);
    color: white;
    border: none;
    border-radius: 12px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.main-action-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.is-edit {
    width: 75%;
}

/* Адаптивность */
@media (max-width: 480px) {
    .telegram-container {
        padding: 15px;
    }

    .stats-grid,
    .stats-grid-two {
        grid-template-columns: 1fr;
    }

    .header h1 {
        font-size: 20px;
    }
}
/* Стили для ошибок */
.input-group {
    position: relative;
}

.error {
    border: 2px solid #ff4757 !important;
    background-color: rgba(255, 71, 87, 0.1) !important;
}

.error-message {
    color: #ff4757;
    font-size: 12px;
    margin-top: 5px;
    display: block;
}

/* Улучшение доступности */
input:focus-visible,
select:focus-visible,
textarea:focus-visible,
button:focus-visible {
    outline: 2px solid #0088cc;
    outline-offset: 2px;
}

/* Анимации для плавного появления ошибок */
.error-message {
    animation: slideDown 0.3s ease-out;
}

@keyframes slideDown {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.form-container {
    max-width: 100%;
    min-height: 100vh;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    padding: 0;
}

.form-header {
    padding: 20px;
    background: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(10px);
    position: sticky;
    top: 0;
    z-index: 100;
}

.form-header h1 {
    margin: 10px 0 5px 0;
    font-size: 20px;
}

.form-header p {
    margin: 0;
    opacity: 0.8;
    font-size: 14px;
}

.back-button {
    background: rgba(255, 255, 255, 0.1);
    border: none;
    color: white;
    font-size: 20px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.adaptive-form {
    padding: 20px;
    padding-bottom: 100px;
}

.form-section {
    border: none;
    margin: 0 0 25px 0;
    padding: 20px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 16px;
    backdrop-filter: blur(10px);
}

.form-section legend {
    font-weight: 600;
    font-size: 16px;
    padding: 0 10px;
    margin-bottom: 15px;
}

.input-group {
    margin-bottom: 20px;
}

.input-row {
    display: flex;
    gap: 15px;
    margin-bottom: 20px;
}

.input-group.half {
    flex: 1;
}

label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
    font-size: 14px;
}

label.required::after {
    content: " *";
    color: #ff4757;
}

input, select, textarea {
    width: 100%;
    padding: 12px 16px;
    border: none;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.9);
    color: #333;
    font-size: 16px;
    transition: all 0.3s ease;
    box-sizing: border-box;
}

input:focus, select:focus, textarea:focus {
    outline: none;
    background: white;
    box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.3);
}

textarea {
    resize: vertical;
    min-height: 80px;
    font-family: inherit;
}

/* Приоритет кнопки */
.priority-buttons {
    display: flex;
    gap: 10px;
}

.priority-btn {
    flex: 1;
    padding: 10px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    background: transparent;
    color: white;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.priority-btn.active {
    background: rgba(255, 255, 255, 0.2);
    border-color: rgba(255, 255, 255, 0.6);
}

/* Теги */
.tags-input {
    position: relative;
}

.tags-list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-top: 10px;
}

.tag {
    display: inline-flex;
    align-items: center;
    background: rgba(255, 255, 255, 0.2);
    padding: 6px 12px;
    border-radius: 20px;
    font-size: 14px;
    gap: 5px;
}

.tag button {
    background: none;
    border: none;
    color: white;
    cursor: pointer;
    font-size: 16px;
    padding: 0;
    width: 16px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Переключатели */
.switch-group {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 15px;
}

.switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 28px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.3);
    transition: .4s;
    border-radius: 34px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: #4CD964;
}

input:checked + .slider:before {
    transform: translateX(22px);
}

/* Кнопки действий */
.form-actions {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 20px;
    background: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(20px);
    display: flex;
    gap: 15px;
}

.btn-primary, .btn-secondary {
    flex: 1;
    padding: 16px;
    border: none;
    border-radius: 12px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.btn-primary {
    background: #0088cc;
    color: white;
}

.btn-primary:active {
    background: #006699;
    transform: scale(0.98);
}

.btn-secondary {
    background: rgba(255, 255, 255, 0.1);
    color: white;
}

.btn-secondary:active {
    background: rgba(255, 255, 255, 0.2);
    transform: scale(0.98);
}

/* Адаптивность для мобильных устройств */
@media (max-width: 480px) {
    .form-container {
        padding: 0;
    }

    .adaptive-form {
        padding: 15px;
        padding-bottom: 90px;
    }

    .form-section {
        padding: 15px;
        margin-bottom: 20px;
    }

    .input-row {
        flex-direction: column;
        gap: 0;
    }

    .input-group.half {
        flex: none;
    }

    .priority-buttons {
        flex-direction: column;
    }

    .form-actions {
        padding: 15px;
        flex-direction: column;
    }

    input, select, textarea {
        padding: 14px 16px;
        font-size: 16px; /* Увеличиваем для удобства на мобильных */
    }

    /* Улучшаем доступность для touch устройств */
    input, select, textarea, button {
        min-height: 44px;
    }
}

/* Планшеты и большие экраны */
@media (min-width: 768px) {
    .form-container {
        max-width: 600px;
        margin: 0 auto;
        border-radius: 0;
    }

    .adaptive-form {
        padding: 30px;
        padding-bottom: 120px;
    }
}

/* Анимации */
@keyframes slideIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.form-section {
    animation: slideIn 0.3s ease-out;
}

/* Кастомный скроллбар */
::-webkit-scrollbar {
    width: 6px;
}

::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
}

::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.3);
    border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.5);
}